import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import jsonp from 'jsonp';

const access_token = 'db0b9ee8cffa6bf13e08e860deeaac6cc6681d436d32de1b14bea6cda05f88ac3abae28661c39271ac50c';

class App extends React.Component {

	state = {
		events: [],
		token: ''
	};
	componentDidMount() {
		connect.subscribe((e) => {
			this.setState((prevState) => {
				return {
					events: [...prevState.events, e.detail.type]
				}
			})
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				case 'VKWebAppAccessTokenReceived': 
					this.setState({ token: e.data.access_token })
					break;
				case 'vk-connect': 
					console.log(e);
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		connect.send("VKWebAppGetAuthToken", {"app_id": 7087022, "scope": "messages"});
	}

	render() {
		return (
			<div>{this.state.token}</div>
		);
	}
}

export default App;
